.form {
  max-width: 300px;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 auto;
  display: flex;
}

.label {
  margin-bottom: 10px;
}

input[type="number"] {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
}

.createBtn {
  color: #fff;
  cursor: pointer;
  background-color: #337ab7;
  border: none;
  border-radius: 4px;
  margin-top: 10px;
  padding: 8px 16px;
}

.createBtn:hover {
  background-color: #23527c;
}

